.badge {
    display: inline-flex;
    align-items: center;
    gap: var(--space-1);
    font-weight: 600;
    border-radius: var(--radius-full);
    text-transform: uppercase;
    letter-spacing: 0.05em;
    white-space: nowrap;
}

/* Sizes */
.sm {
    padding: 2px var(--space-2);
    font-size: 0.625rem;
}

.md {
    padding: var(--space-1) var(--space-3);
    font-size: 0.75rem;
}

.lg {
    padding: var(--space-2) var(--space-4);
    font-size: 0.875rem;
}

/* Variants */
.primary {
    background: linear-gradient(135deg, var(--primary-600), var(--primary-700));
    color: white;
}

.success {
    background: linear-gradient(135deg, #059669, #047857);
    color: white;
}

.warning {
    background: linear-gradient(135deg, var(--accent-amber), #d97706);
    color: #1a1a1a;
}

.danger {
    background: linear-gradient(135deg, var(--accent-rose), #dc2626);
    color: white;
}

.info {
    background: linear-gradient(135deg, var(--accent-cyan), #0891b2);
    color: white;
}

.outline {
    background: transparent;
    border: 1px solid var(--border-subtle);
    color: var(--text-secondary);
}

/* Glow Effect */
.glow.primary {
    box-shadow: 0 0 20px rgba(139, 92, 246, 0.5);
}

.glow.success {
    box-shadow: 0 0 20px rgba(5, 150, 105, 0.5);
}

.glow.warning {
    box-shadow: 0 0 20px rgba(245, 158, 11, 0.5);
}

.glow.danger {
    box-shadow: 0 0 20px rgba(244, 63, 94, 0.5);
}

.glow.info {
    box-shadow: 0 0 20px rgba(6, 182, 212, 0.5);
}

/* Icon */
.icon {
    display: flex;
    align-items: center;
    justify-content: center;
}

.icon svg {
    width: 12px;
    height: 12px;
}

.lg .icon svg {
    width: 16px;
    height: 16px;
}

/* Achievement Badge */
.achievementBadge {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--space-2);
    padding: var(--space-4);
    border-radius: var(--radius-xl);
    background: var(--bg-card);
    border: 1px solid var(--border-subtle);
    position: relative;
    cursor: pointer;
    transition: all var(--transition-normal);
}

.achievement-sm {
    padding: var(--space-2);
    gap: var(--space-1);
}

.achievement-md {
    padding: var(--space-4);
    gap: var(--space-2);
}

.achievement-lg {
    padding: var(--space-6);
    gap: var(--space-3);
}

.unlocked {
    border-color: rgba(139, 92, 246, 0.5);
    background: linear-gradient(135deg,
            rgba(139, 92, 246, 0.15) 0%,
            rgba(236, 72, 153, 0.1) 100%);
    box-shadow: 0 0 30px rgba(139, 92, 246, 0.3);
}

.locked {
    opacity: 0.5;
    filter: grayscale(0.8);
}

.achievementEmoji {
    font-size: 2rem;
}

.achievement-sm .achievementEmoji {
    font-size: 1.25rem;
}

.achievement-lg .achievementEmoji {
    font-size: 3rem;
}

.achievementName {
    font-size: 0.75rem;
    font-weight: 600;
    text-align: center;
    color: var(--text-secondary);
}

.achievement-lg .achievementName {
    font-size: 0.875rem;
}

.lockOverlay {
    position: absolute;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.3);
    border-radius: var(--radius-xl);
    font-size: 1.5rem;
}

.achievement-sm .lockOverlay {
    font-size: 1rem;
}